.container {
	width: 100%;
	position: relative;
	display: flex;
	flex-direction: column;
}
.Article {
	width: 60%;
	margin: 3rem auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: left;
	position: relative;
}

.mainImage {
	width: 100%;
	height: 100%;
	margin-bottom: 1rem;
}
.details {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
}
.details .date,
.details .contributor {
	font-style: italic;
	font-size: 20px;
	padding: 1rem 0;
}

.contributor a {
	color: royalblue;
	text-decoration: underline;
}
.contributor a:hover {
	opacity: 0.6;
}

.body img {
	float: right;
	clear: both;
	width: 60%;
	height: 100%;
	margin-left: 1rem;
}
.body p {
	margin: 1rem 0;
	font-size: 16px;
	text-align: justify;
}
.body a {
	color: royalblue;
	text-decoration: underline;
	font-size: 16px;
}

.backBtn {
	margin-top: 1rem;
}

@media screen and (max-width: 700px) {
	.container {
		font-size: 75%;
	}
	.Article {
		width: 90%;
	}
}
